<template>
  <div>
    <p>名称：{{formData.name}}</p>
    <!-- <div class="cost-result" v-show="step===0">
      <p>租金：{{rent}}</p>
      <p>人力成本：{{laborCost}}</p>
      <p>杂费：{{formData.otherCost}}</p>
    </div>
    <div class="income-result" v-show="step===1||step===2">
      <div v-for="(item,index) in formData.category" :key="index">
        <p>品类：{{item.name}}</p>
        <p>销售额：{{formData.sales*item.saleRate/100}}</p>
        <p>毛利额：{{formData.sales*item.saleRate*item.grossProfitRate/10000}}</p>
        <p>固返额：{{formData.sales*item.saleRate*item.stableReturnRate/10000}}</p>
        <p>月返额：{{formData.sales*item.saleRate*item.returnRatePerMonth/10000*0.9}}</p>
        <hr />
      </div>
      <p>销售额：{{formData.sales}}</p>
      <p>季返额：{{formData.returnValuePerQuarter/3*0.9}}</p>
    </div><hr /> -->
    <p>总收入：{{income}}</p>
    <p>总成本：{{cost}}</p>
    <p>总盈亏：{{income-cost}}</p>
  </div>
</template>

<script>
  export default {
    props: {
      formData: Object,
      step: Number
    },
    computed: {
      rent() {
        return this.formData.area * this.formData.costPerArea;
      },
      laborCost() {
        return this.formData.personNumber * this.formData.costPerPerson;
      },
      cost() {
        return this.rent + this.laborCost + this.formData.otherCost;
      },
      income() {
        if (this.formData.category) {
          return this.formData.category.reduce((sum, item) => {
            return (
              sum +
              this.formData.sales * item.saleRate * item.grossProfitRate / 10000 +
              this.formData.sales *
                item.saleRate *
                item.stableReturnRate /
                10000 +
              this.formData.sales *
                item.saleRate *
                item.returnRatePerMonth /
                10000 *
                0.9
            );
          }, this.formData.returnValuePerQuarter / 3 * 0.9);
        } else {
          return 0;
        }
      }
    }
  };
</script>

<style>

</style>



