<template>
  <div>
    <Row :gutter="10" class="mb-10">
      <Col span="16">
      <Card>
        <p slot="title">
          <Icon type="ios-paper"></Icon>
          Form
        </p>
        <Row type="flex" justify="center">
          <Col span="16">
          <information-form-step :step="step"></information-form-step>
          </Col>
        </Row>
        <Row type="flex" justify="center">
          <Col span="20">
          <information-form :step="step" @changeFormData="changeFormData" @changeStep="changeStep"></information-form>
          </Col>
        </Row>
      </Card>
      </Col>
      <Col span="8">
      <Row :class="$store.state.shrink?'fixed-shrink':'fixed'">
        <Col span="24">
        <Card>
          <p slot="title">
            <Icon type="stats-bars"></Icon>
            Chart
          </p>
          <information-chart :step="step" :formData="formData"></information-chart>
        </Card>
        </Col>
        <Col span="24" class="mt-10">
        <Card>
          <p slot="title">
            <Icon type="ios-paper"></Icon>
            Result
          </p>
          <computed-information :formData="formData" :step="step"></computed-information>
        </Card>
        </Col>
      </Row>
      </Col>
    </Row>
  </div>
</template>

<script>
  import InformationFormStep from "./main-components/InformationFormStep.vue";
  import InformationForm from "./main-components/InformationForm.vue";
  import ComputedInformation from "./main-components/ComputedInformation.vue";
  import InformationChart from "./main-components/InformationChart";

  export default {
    components: {
      InformationFormStep,
      InformationForm,
      ComputedInformation,
      InformationChart
    },
    data() {
      return {
        step: 0,
        formData: {}
      };
    },
    methods: {
      changeStep(dir) {
        if (dir === "next") {
          this.step = Math.min(3, ++this.step);
        } else {
          this.step = Math.max(0, --this.step);
        }
      },
      changeFormData(formData) {
        this.formData = formData;
      }
    }
  };
</script>

<style scoped>
  .mt-10 {
    margin-top: 10px;
  }
  .mb-10 {
    margin-bottom: 10px;
  }
  .fixed-shrink {
    position: fixed;
    width: 31%;
    transition: all 0.5s;
  }
  .fixed {
    position: fixed;
    width: 27%;
    transition: all 0.5s;
  }
</style>



