<template>
  <div>
    <div id="costChart" class="chart"></div>
    <div id="incomeChart" class="chart"></div>
  </div>
</template>

<script>
  import echarts from "echarts";

  export default {
    props: {
      formData: Object,
      step: Number
    },
    data() {
      return {
        costChart: null,
        incomeChart: null
      };
    },
    computed: {
      rent() {
        return this.formData.area * this.formData.costPerArea;
      },
      laborCost() {
        return this.formData.personNumber * this.formData.costPerPerson;
      },
      income() {
        if (this.formData.category) {
          return this.formData.category.reduce((sum, item) => {
            return (
              sum +
              this.formData.sales * item.saleRate * item.grossProfitRate / 10000 +
              this.formData.sales *
                item.saleRate *
                item.stableReturnRate /
                10000 +
              this.formData.sales *
                item.saleRate *
                item.returnRatePerMonth /
                10000 *
                0.9
            );
          }, this.formData.returnValuePerQuarter / 3 * 0.9);
        } else {
          return 0;
        }
      },
      names() {
        if (this.formData.category) {
          return this.formData.category.reduce((sum, item) => {
            return sum + item.name;
          }, "");
        } else {
          return "";
        }
      }
    },
    watch: {
      rent() {
        this.drawCostChart();
      },
      laborCost() {
        this.drawCostChart();
      },
      "formData.otherCost"() {
        this.drawCostChart();
      },
      income() {
        this.drawIncomeChart();
      },
      names() {
        this.drawIncomeChart();
      }
    },
    methods: {
      drawCostChart() {
        this.costChart.setOption({
          title: {
            text: "支出详情",
            x: "center"
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            left: "left",
            data: ["租金支出", "人力支出", "杂费支出"]
          },
          series: [
            {
              name: "支出详情",
              type: "pie",
              radius: "55%",
              center: ["50%", "60%"],
              data: [
                { value: this.laborCost, name: "人力支出" },
                { value: this.formData.otherCost, name: "杂费支出" },
                { value: this.rent, name: "租金支出" }
              ]
            }
          ]
        });
      },
      drawIncomeChart() {
        this.incomeChart.setOption({
          title: {
            text: "收入详情",
            x: "center"
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: "vertical",
            left: "left",
            data: this.formData.category.map(item => item.name)
          },
          series: [
            {
              name: "收入详情",
              type: "pie",
              radius: "55%",
              center: ["50%", "60%"],
              data: this.formData.category.map(item => {
                return {
                  name: item.name,
                  value:
                    this.formData.sales *
                      item.saleRate *
                      item.grossProfitRate /
                      10000 +
                    this.formData.sales *
                      item.saleRate *
                      item.stableReturnRate /
                      10000 +
                    this.formData.sales *
                      item.saleRate *
                      item.returnRatePerMonth /
                      10000 *
                      0.9
                };
              })
            }
          ]
        });
      }
    },
    mounted() {
      this.costChart = echarts.init(document.getElementById("costChart"));
      this.incomeChart = echarts.init(document.getElementById("incomeChart"));
      window.addEventListener("resize", () => {
        this.costChart.resize();
        this.incomeChart.resize();
      });
    }
  };
</script>

<style scoped>
  .chart {
    display: block;
    width: 350px;
    height: 200px;
  }
</style>



