<template>
  <div class="information-form">

    <div class="basic-information" v-show="step===0">
      <Form :label-width="100">
        <h1>基本信息</h1>
        <FormItem label="名称">
          <Input v-model="formData.name"></Input>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="店铺面积">
          <Slider v-model="formData.area" show-input :max="200"></Slider>
        </FormItem>
        <FormItem label="单位面积月租金">
          <InputNumber v-model="formData.costPerArea" :step="100" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="店铺总月租金">
          <span class="total">{{formData.area*formData.costPerArea}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="店员数">
          <Slider v-model="formData.personNumber" show-input :max="20"></Slider>
        </FormItem>
        <FormItem label="单人月成本">
          <InputNumber v-model="formData.costPerPerson" :step="1000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="人工小计">
          <span class="total">{{formData.personNumber*formData.costPerPerson}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="杂费">
          <InputNumber v-model="formData.otherCost" :step="1000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="杂费合计">
          <span class="total">{{formData.otherCost}}</span>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="支出合计">
          <span class="total">{{formData.area*formData.costPerArea+formData.personNumber*formData.costPerPerson+formData.otherCost}}</span>
        </FormItem>
        <Row type="flex" justify="center">
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div>

    <div class="sale" v-show="step===1">
      <Form :label-width="100">
        <h1>销售品类</h1>
        <FormItem label="销售额">
          <InputNumber v-model="formData.sales" :step="10000" :min="0"></InputNumber>
        </FormItem>
        <div class="divider"></div>
        <FormItem label="季返基数">
          <InputNumber v-model="formData.returnValuePerQuarter" :step="10000" :min="0"></InputNumber>
        </FormItem>
        <FormItem label="月季返基数">
          <span class="total">{{Math.floor(formData.returnValuePerQuarter/3*0.9)}}</span>
        </FormItem>
        <div class="divider"></div>
        <template v-for="(item,index) in formData.category">
          <div :key="index">
            <FormItem label="品类名称" required>
              <Row>
                <Col span='20'>
                <AutoComplete v-model="item.name" :data="categoryList" :filter-method="filterMethod"></AutoComplete>
                </Col>
                <Col span='4'>
                <Button class="remove-btn" @click="removeCategory(index)">remove</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem label="销售占比">
              <Slider v-model="item.saleRate" show-input></Slider>
            </FormItem>
            <FormItem label="销售额">
              <span class="total">{{formData.sales*item.saleRate/100}}</span>
            </FormItem>
            <FormItem label="毛利率">
              <Slider v-model="item.grossProfitRate" show-input :max="15"></Slider>
            </FormItem>
            <FormItem label="毛利润">
              <span class="total">{{formData.sales*item.saleRate*item.grossProfitRate/10000}}</span>
            </FormItem>
            <FormItem label="固返比例">
              <Slider v-model="item.stableReturnRate" show-input :max="20"></Slider>
            </FormItem>
            <FormItem label="固返额">
              <span class="total">{{formData.sales*item.saleRate*item.stableReturnRate/10000}}</span>
            </FormItem>
            <FormItem label="月返比例">
              <Slider v-model="item.returnRatePerMonth" show-input :max="20"></Slider>
            </FormItem>
            <FormItem label="月返额">
              <span class="total">{{formData.sales*item.saleRate*item.returnRatePerMonth/10000}}</span>
            </FormItem>
            <div class="divider"></div>
          </div>
        </template>
        <Row type="flex" justify="center">
          <Col span="12">
          <Button type="dashed" long @click="addCategory" icon="plus-round">Add category</Button>
          </Col>
        </Row>
        <div class="divider"></div>
        <FormItem label="销售总收入">
          <span class="total">{{formData.category.reduce((sum, item) => { return ( sum + this.formData.sales * item.saleRate * item.grossProfitRate / 10000 + this.formData.sales * item.saleRate * item.stableReturnRate / 10000 + this.formData.sales * item.saleRate * item.returnRatePerMonth / 10000 * 0.9 ); }, this.formData.returnValuePerQuarter / 3 * 0.9)}}</span>
        </FormItem>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div>

    <!-- <div class="sale-rate" v-show="step===2">
      <Form :label-width="100">
        <h1>销售结构</h1>
        <template v-for="(item,index) in formData.category">
          <div :key="index">
            <FormItem label="品类名称">
              <Row>
                <Col span='20'>
                <span>{{item.name}}</span>
                </Col>
                <Col span='4'>
                <Button class="remove-btn" @click="removeCategory(index)">remove</Button>
                </Col>
              </Row>
            </FormItem>
            <FormItem label="毛利率">
              <Slider v-model="item.grossProfitRate" show-input :max="15"></Slider>
            </FormItem>
            <FormItem label="销售占比">
              <Slider v-model="item.saleRate" show-input></Slider>
            </FormItem>
            <div class="divider"></div>
          </div>
        </template>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
          <Col span="4">
          <Button @click="changeStep('next')">下一步</Button>
          </Col>
        </Row>
      </Form>
    </div> -->

    <div class="other-information" v-show="step===2">
      <Form :label-width="100">
        <h1>其他信息</h1>
        <Row type="flex" justify="center" :gutter="10" class="mt-10">
          <Col span="4">
          <Button @click="changeStep('prev')">上一步</Button>
          </Col>
        </Row>
      </Form>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      step: Number
    },
    data() {
      return {
        formData: {
          name: "默认店铺",
          area: 100,
          costPerArea: 600,
          personNumber: 8,
          costPerPerson: 5000,
          otherCost: 5000,
          sales: 600000,
          returnValuePerQuarter: 40000,
          category: []
        },
        categoryList: [
          "DSC",
          "ILC",
          "Lens",
          "PV",
          "LCD",
          "MDR",
          "SPK",
          "DMP",
          "PS",
          "Mobile",
          "SB",
          "Others"
        ]
      };
    },
    methods: {
      changeStep(dir) {
        if (this.step === 1) {
          if (this.formData.category.some(item => item.name === "")) {
            this.$Message.error("品类名称不能为空");
          } else {
            this.$emit("changeStep", dir);
          }
        } else {
          this.$emit("changeStep", dir);
        }
      },
      changeFormData() {
        this.$emit("changeFormData", this.formData);
      },
      addCategory() {
        this.formData.category.push({
          name: "",
          grossProfitRate: 5,
          stableReturnRate: 5,
          returnRatePerMonth: 3,
          saleRate: 0
        });
      },
      removeCategory(index) {
        this.formData.category.splice(index, 1);
      },
      filterMethod(value, option) {
        return option.toUpperCase().indexOf(value.toUpperCase()) !== -1;
      }
    },
    mounted() {
      this.changeFormData();
    }
  };
</script>

<style scoped>
  h1 {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .mt-10 {
    margin-top: 20px;
  }
  .divider {
    width: 90%;
    margin: 20px auto;
    border-bottom: 2px dashed #f0f0f0;
  }
  .remove-btn {
    margin-left: 17px;
  }
  .total {
    font-size: 15px;
    font-weight: bold;
  }
</style>



