<template>
  <div class="step">
    <Steps :current="step">
      <Step title="基本信息" content="店铺基本信息"></Step>
      <Step title="销售信息" content="店铺销售信息"></Step>
      <!-- <Step title="销售结构" content="店铺销售结构"></Step> -->
      <Step title="其他信息" content="店铺其他信息"></Step>
    </Steps>
  </div>
</template>

<script>
  export default {
    props: {
      step: Number
    }
  };
</script>

<style scoped>
  .step {
    margin-top: 20px;
  }
</style>



